
import  { Link }  from '../routes';
import { device } from '../breakpoints'

export default class Header extends React.Component{


    state = {
        isOpened: false,
        navBarTrasparent : false
    }
    


    componentDidMount(){
        window.onscroll = () =>{
            if(window.pageYOffset >=100){
                this.setState({
                    navBarTrasparent : true
                })
            }else{
                this.setState({
                    navBarTrasparent : false
                })
            }
        }
    }

    handlerClickButtonBurguer = (e) => {
        let currentState = this.state
        this.setState({
            isOpened:!currentState.isOpened
        })
    }

    render(){
        return(
            <header className={(this.state.navBarTrasparent ? "background-navbar" : "")}>
                
                <Link key="home" route="/"><img className="logo" src="/static/logo.svg"/></Link>


                <div className={"hamburguer-icon " +  (this.state.isOpened ? "opened" : "")  } onClick={this.handlerClickButtonBurguer}>
                    <div className="hamburguer-line line1"></div>
                    <div className="hamburguer-line line2"></div>
                    <div className="hamburguer-line line3"></div>
                </div>

                <nav className={"menu-navigation " +  (this.state.isOpened ? "opened" : "")  }>
                    <Link href="/"><a>Inicio</a></Link>
                    <Link href="/productos"><a>Productos</a></Link>
                    <Link href="/Contacto"><a>Contacto</a></Link>
                </nav>

                <style jsx>
                    {
                        `
                        
                        header{
                            display:flex;
                            width:100%;
                            position:fixed;
                            z-index:100;
                            top:0;
                            flex-direction:row;
                            align-items:center;
                            justify-content:space-between;
                        }

                        .background-navbar{
                            background: black; 
                            height:100px;
                        }

                        header .logo{
                            margin:20px;
                            max-width: 50px;
                            cursor: pointer;
                        }

                        header .menu-navigation{
                            width:100vw;
                            height:100%;
                            background: rgba(52,105,154,1);
                            background: linear-gradient(to bottom, rgba(52,105,154,1) 0%, rgba(17,63,103,1) 95%, rgba(17,63,103,1) 100%);
                            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#34699a', endColorstr='#113f67', GradientType=0 );
                            position:fixed;
                            top:0;
                            display:none;
                            align-items:center;
                            justify-content:center;
                            flex-direction: column;
                            z-index:102;
                            transition:.3s all;
                        }

                        header .menu-navigation a{
                            opacity:0;
                            margin: 10px 0;
                            text-decoration:none;
                            color:#fff;
                            padding: 10px 20px;
                            font-size:1.1em;
                            text-transform: uppercase;
                        }
                            
                        header .menu-navigation.opened{
                            display:flex;
                            animation-fill-mode: forwards; 
                            animation: fadeIn .5s;
                        }

                        header .menu-navigation.opened a:nth-child(1) {
                            animation-name: fadeIn;
                            animation-fill-mode: forwards; 
                            animation-duration: .5s;
                            animation-delay: .1s;

                        }

                        
                        header .menu-navigation.opened a:nth-child(2) {
                            animation-name: fadeIn;
                            animation-fill-mode: forwards; 
                            animation-duration: .5s;
                            animation-delay: .4s;

                        }

                        
                        header .menu-navigation.opened a:nth-child(3) {
                            animation-name: fadeIn;
                            animation-fill-mode: forwards; 
                            animation-duration: .5s;
                            animation-delay: .7s;

                        }

                        
                        header .menu-navigation.opened a:nth-child(4) {
                            animation-name: fadeIn;
                            animation-fill-mode: forwards; 
                            animation-duration: .5s;
                            animation-delay: 1s;

                        }

                        .hamburguer-icon{
                            position:absolute;
                            right:10px;
                            cursor:pointer;
                            z-index:1000;
                            transition: all 0.325s ease;
                        }



                        .hamburguer-icon .hamburguer-line {
                            width: 25px;
                            margin: 10px;
                            border: 2px solid #113F67;
                            transition: all 0.325s ease;
                            border-radius: 12px;
                            position:relattive;
                            z-index:103;
                        }

    
                        .hamburguer-icon.opened .hamburguer-line {
                            border: 2px solid white;
                        }

                        .hamburguer-icon.opened .line1 {
                            transform: rotate(45deg) translate(19px);
                        }

                        .hamburguer-icon.opened .line2 {
                            opacity: 0;
                        }

                        .hamburguer-icon.opened .line3 {
                            transform: rotate(-45deg) translate(20px);
                        }

                        @keyframes fadeIn{
                            0%   { opacity: 0; }
                            100% { opacity: 1; }
                        }

                        @media ${device.tablet}{


                            .hamburguer-icon{
                                display:none;
                            }

                            header{
                                align-items:space-between;
                                justify-content:space-between;
                            }

                            header .menu-navigation{
                                display:flex;
                                position:relative;
                                background:none;
                                width:auto;
                                height:auto;
                                flex-direction: row;
                            }

                            header .menu-navigation a{
                                opacity:1;
                                color:#113F67;
                                margin:0 10px;
                                padding:0;
                                font-size: 1em;
                                text-transform: initial;
                                transition: all .4s; 

                            }

                            header .menu-navigation a:hover{
                                transform: scale(1.1);
                                padding:0 5px;
                            }

                        }

                        @media ${device.tablet}{
                            
                            nav{
                                position:relative;
                                left:-50px;
                            }

                            header .logo{
                                max-width:60px;
                                position:relative;
                                left: 50px
                            }
                            header .menu-navigation{
                                font-size:1.05em;
                            }
                        
                        }
                        
                        `
                    }
                </style>
            </header>
        )
    }
}